<template>
  <div style="width:100%;height:100%;" class="backImage" :style="background">
    <div class="logoImag">
      <img src="static/imgs/login.png" alt srcset />
    </div>
    <div class="APPTitle">
      <!-- <div>
        <span>宝安区</span>
      </div>-->
      <div>宝安区治水提质水质动态监测系统</div>
    </div>
    <div class="containerLogin">
      <div class="borderBottom">
        <!-- <div class="imageSize">
          <img src="../../static/LoginImage/user.png" alt />
        </div>-->
        <div style="flex:1">
          <input
            type="text"
            class="input"
            placeholder="用户名"
            v-model="inputValue"
            @focus="showCopy=false"
            @blur="showCopy=true"
          />
        </div>
      </div>
      <div class="borderBottom">
        <!-- <div class="imageSize">
          <img src="../../static/LoginImage/password.png" alt />
        </div>-->
        <div style="flex:1">
          <input
            :type="inputflag?'text':'password'"
            class="input"
            placeholder="密码"
            v-model="inputpasswordValue"
            @focus="showCopy=false"
            @blur="showCopy=true"
          />
          <!-- <img
            @click="inputflag=!inputflag"
            :src="inputflag?'../../static/LoginImage/user.png':'../../static/LoginImage/password.png'"
            alt
            srcset
          />-->
        </div>
      </div>
    </div>
    <!-- <div class="checkboxPassword">
      <div class="remenber">
        <input type="checkbox" checked="checked" id="usename" v-model="checked" />
        <span :style="colorPassword">记住密码</span>
      </div>
    </div>-->
    <div class="Login">
      <div class="submitBtn" @click="submit()">
        <div>
          <span>登录</span>
        </div>
      </div>
    </div>
    <div class="Login">
      <div class="bottomBtn">
        <span @click="RegisterTo()">注册</span>
        <span @click="resetLoginPassword()">忘记密码？</span>
      </div>
    </div>
    <div class="bottomAbBottom" v-show="showCopy===true">
      <span>copyright©安志生态2019</span>
    </div>
  </div>
</template>
<script>
import AppBace from "../../static/ajax.js";
// import NewsLocaltion from "../../static/js/index.js";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  data() {
    return {
      showCopy: true,
      inputflag: false,
      resetcolor: "color:#ccc",
      passwordFalse: true,
      tips: "",
      listGo: [],
      type: "",
      loading: false,
      inputpasswordValue: "",
      form: {
        input: ""
      },
      inputValue: "",
      // text:"hh",
      loading1: false,
      color: "",
      index: 0,
      validateForm: {
        username: "",
        password: "",
        isAgree: true,
        memorry: false
      },
      // 账户密码的创建值，临时用
      token: "",
      user: [],
      checked: true,
      colorPassword: "color:#0052d6",
      LoginStatus: false,
      background: ""
    };
  },
  created() {
    
    // 监听是否有登入的状态存在
    this.background = {
      // backgroundImage:
      //   "url(http://imgq.duitang.com/uploads/blog/201308/09/20130809054800_dsZZU.thumb.700_0.gif)",
      // backgroundRepeat: "no-repeat",
      // backgroundPosition: "center"
    };
  },
  mounted() {
    this.getCookie();
    // setInterval(() => {
    //   this.localtionNews();
    // }, 10000);
    // this.localtionNews();

    // this.localtionNews();
    //监听
  },
  methods: {
    LoginlocalStorage() {
      // this.loginUser = localStorage.getItem("user")
    },
    RegisterTo() {
      this.$router.push("/Register");
    },
    forget() {},
    // app的通知栏内容
    localtionNews() {
      cordova.plugins.notification.local.schedule([
        {
          id: 0,
          title: "宝安区治水提质动态监测系统",

          text: "感谢使用我们的监测系统，使用的意见欢迎给我留言！",

          foreground: true
        },
        {
          id: 1,
          title: "版权所属(版本1.0.0)",

          text: "深圳安志生态环境有限公司",

          foreground: true
        }
      ]);
    },
    resetLoginPassword() {
      localStorage.clear();
      // this.resetcolor = "color:blue";
      this.inputValue = "";
      this.inputpasswordValue = "";
      this.$router.push("/Forget");
      // setTimeout(() => {
      //   this.resetcolor = "color:#ccc";
      // }, 200);
    },
    clearPassword() {
      this.inputpasswordValue = "";
    },
    clearTips() {
      this.passwordFalse = false;
      this.inputValue = "";
    },
    async submit(e) {
      // this.loading1 = !this.loading1
      // this.localtionNews();
      this.submitForm();
      this.fullscreen();
      setTimeout(() => {
        if (this.LoginStatus === false) {
          this.loading.close();
          Toast("登录超时，请重试！");
        }
      }, 10000);
      console.log(this.$store.state.menu);
      this.$store.commit("deleteDataMenu", 1);
      var password = this.inputpasswordValue;
      var user = this.inputValue;
      var url = "/login?account=" + user + "&&password=" + password;
      var res = await AppBace.request(url, "POST");
      console.log("login", res);

      if (res.message == "Network Error") {
        this.passwordFalse = true;
        this.tips = "网络错误！！！";
        // this.clearTips();
        Toast(this.tips);
        this.loading.close();
        setTimeout(() => {
          this.clearTips();
        }, 3000);
      }
      this.loading.close();
      var data = res.data.message;
      if (data === "success") {
        this.LoginStatus = true;
        this.$router.push("/Home");
        Toast(res.data.info);
        sessionStorage.setItem("user", res.data.data.roles);
        sessionStorage.setItem("username", res.data.data.user.username);
        sessionStorage.setItem("token", res.data.data.token);
        sessionStorage.setItem("name", res.data.data.user.account);
        sessionStorage.setItem("phone", res.data.data.user.phone);
        // 保存登录信息
        localStorage.setItem("token", res.data.data.token);
        localStorage.setItem("user", res.data.data.roles);
        localStorage.setItem("username", res.data.data.user.username);
        localStorage.setItem("name", res.data.data.user.account);
        // debugger
        localStorage.setItem("destination", res.data.data.destination);

        this.$store.commit("permisions", res.data.data.app.children);
        // this.$store.commit("usernameLocal",res.data.data.)
        //  await this.menuPoint();

        this.loading.close();
      } else if (data == "fail") {
        // this.$toast.error(data);
        this.passwordFalse = true;
        this.tips = res.data.info;
        // this.clearTips();
        Toast(this.tips);
        setTimeout(() => {
          this.clearTips();
        }, 3000);
      } else if (data === "用户名或密码错误") {
        // this.$toast.error(data);
        this.passwordFalse = true;
        this.tips = data;
        // this.clearTips();
        Toast(data);
        setTimeout(() => {
          this.clearTips();
        }, 3000);
        this.loading.close();
      } else {
        Toast(res.data.info);

        this.tips = res.data.info;

        setTimeout(() => {
          this.clearTips();
        }, 3000);
        this.loading.close();
      }
    },
    fullscreen() {
      this.loading = this.$loading();
    },

    submitForm(formName) {
      const self = this;
      //判断复选框是否被勾选 勾选则调用配置cookie方法
      if (self.checked === true) {
        console.log("checked == true");
        //传入账号名，密码，和保存天数3个参数
        self.setCookie(self.inputValue, self.inputpasswordValue, 7);
      } else if (self.inputValue === "") {
        // console.log("清空Cookie");
        //清空Cookie
        self.clearCookie();
        // this.getCookie();
      }

      //与后端请求代码，本功能不需要与后台交互所以省略
    },
    //设置cookie
    setCookie(c_name, c_pwd, exdays) {
      var exdate = new Date(); //获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数
      //字符串拼接cookie
      window.document.cookie =
        "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
      window.document.cookie =
        "userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
    },
    //读取cookie
    getCookie: function() {
      if (document.cookie.length > 0) {
        console.log("cookie", document.cookie);
        var arr = document.cookie.split("; "); //这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split("="); //再次切割
          //判断查找相对应的值
          if (arr2[0] == "userName") {
            this.inputValue = arr2[1]; //保存到保存数据的地方
          } else if (arr2[0] == "userPwd") {
            this.inputpasswordValue = arr2[1];
          }
        }
      }
    },
    //清除cookie
    clearCookie: function() {
      this.setCookie("", "", -1); //修改2值都为空，天数为负1天就好了
    }
  }
};
</script>
<style scoped>
.backImage {
  background: #0067ff;
  background: -moz-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, #0067ff),
    color-stop(100%, #2bb5ff)
  );
  background: -webkit-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -o-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: -ms-linear-gradient(top, #0067ff 0%, #2bb5ff 100%);
  background: linear-gradient(to bottom, #0067ff 0%, #2bb5ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#2bb5ff',GradientType=0 );
}
h2 {
  margin: 0;
  text-align: center;
  letter-spacing: 8px;
}
.right {
  border-bottom: 4px #ccc solid;
}
#btn1 {
  text-align: center;
  width: 100vw;
  height: 285px;
}
.button-wrapper {
  text-align: center;
}
.mu-button {
  margin: 8px;
  vertical-align: middle;
}
a.mu-button {
  text-decoration: none;
}
#user {
  background: blue;
}
.userInput input {
  border: none;
  width: 100%;
  height: 40px;
  outline: none;
}
.userInput {
  border-bottom: #ccc 1px solid;
}
#loginBottom {
  color: #ccc;
}
.logoImag {
  padding: 80px 120px 20px 120px;
  /* height: 50%; */
}
.logoImag img {
  width: 100%;
  height: auto;
}
#password {
  background: white;
}
input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  -webkit-text-fill-color: #ffffff !important;
  box-shadow: 0 0 0px 1000px transparent inset !important;
  background-color: transparent;
  background-image: none;
  transition: background-color 50000s ease-in-out 0s;
  /* color: white; */
}
input {
  background-color: transparent;
}

.position {
  position: fixed;
  top: 2px;
  right: 5px;
  color: rgb(193, 193, 248);
}
.remenber {
  /* padding: 10px; */
  width: 80%;
  color: #d9d9d9;
}
#checked {
}

input[type="checkbox"] {
  width: 20px;
  height: 20px;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  line-height: 18px;
  margin-right: 10px;
  position: relative;
}

input[type="checkbox"]::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  /* background: #fff; */
  width: 100%;
  height: 100%;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}

input[type="checkbox"]:checked::before {
  content: "\2713";
  background-color: #0052d6;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border: 1px solid #fff;
  border-radius: 4px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}
.containerLogin input::-webkit-input-placeholder {
  color: white !important;
  font-size: 14px;
}
.containerLogin input::-moz-placeholder {
  /* Mozilla Firefox 19+ */

  color: white !important;
  font-size: 14px;
}

.containerLogin input:-ms-input-placeholder {
  /* Internet Explorer 10+ */

  color: white !important;
  font-size: 14px;
}

.APPTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #ffffff;
  /* font-weight: 600; */
  font-size: 20px;
  margin-bottom: 30px;
}
.input {
  border: none;
  height: 39px;
  flex: 1;
  /* padding: 0 10px; */
  color: white;
  width: 100%;
  font-size: 14px;
  opacity: 1;
  background: transparent;
}
.borderBottom {
  border-bottom: 1px solid #ffffff;
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin-bottom: 20px;
  /* color: #d9d9d9; */
}
.containerLogin {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.imageSize img {
  width: 20px;
  height: 20px;
}
.checkboxPassword {
  display: flex;
  justify-content: center;
  height: 30px;
  align-items: center;
}
.submitBtn {
  /* border-radius: 10px; */
  width: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #d9e7ff;
  color: #0875ff;
  font-size: 16px;
  height: 44px;
  border-radius: 5px;
  /* margin-top: 20px; */
  font-weight: 600;
}
.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 20px; */
}
.bottomBtn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  width: 75%;
  color: white;
}
.bottomAbBottom {
  position: absolute;
  bottom: 2%;
  text-align: center;
  width: 100%;
  color: white;
  font-size: 10px;
}
</style>



